"use client"

import { useState, useEffect, useCallback } from "react"
import type { ColumnDef } from "@tanstack/react-table"
import { Edit, Trash2, Plus } from "lucide-react"
import type { Vaccine } from "@/types/vaccine"
import { useStore } from "@/store"
import { DataTable } from "@/components/ui/data-table"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { useToast } from "@/hooks/use-toast"
import { VaccineModal } from "@/components/modals/VaccineModal"

export default function VaccineManagementPage() {
  const { fetchVaccines, deleteVaccine } = useStore.getState()
  const { toast } = useToast()

  const [vaccines, setVaccines] = useState<Vaccine[]>([])
  const [loading, setLoading] = useState(true)
  const [isCreateModalOpen, setIsCreateModalOpen] = useState(false)
  const [isUpdateModalOpen, setIsUpdateModalOpen] = useState<Vaccine | undefined>(undefined)

  const loadVaccines = useCallback(async () => {
    try {
      setLoading(true)
      const data = await fetchVaccines()
      setVaccines(data)
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to load vaccines",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }, [fetchVaccines, toast])

  useEffect(() => {
    loadVaccines()
  }, [loadVaccines])

  const handleDelete = async (vaccineId: number) => {
    try {
      await deleteVaccine(vaccineId)
      toast({
        title: "Success",
        description: "Vaccine deleted successfully",
      })
      await loadVaccines()
    } catch (error) {
      toast({
        title: "Error",
        description: "Failed to delete vaccine",
        variant: "destructive",
      })
    }
  }

  const columns: ColumnDef<Vaccine>[] = [
    {
      accessorKey: "id",
      header: "ID",
    },
    {
      accessorKey: "title",
      header: "Vaccine Name",
    },
    {
      accessorKey: "price",
      header: "Price",
      cell: ({ row }) => {
        const price = row.getValue("price") as number
        return new Intl.NumberFormat("en-US", {
          style: "currency",
          currency: "USD",
        }).format(price)
      },
    },
    {
      accessorKey: "createdAt",
      header: "Created At",
      cell: ({ row }) => {
        const date = row.getValue("createdAt") as string
        return new Date(date).toLocaleDateString()
      },
    },
    {
      id: "actions",
      header: "Actions",
      cell: ({ row }) => {
        const vaccineId = row.getValue("id") as number
        return (
          <div className="flex gap-2">
            <Button
              variant="outline"
              size="sm"
              onClick={() => setIsUpdateModalOpen(row.original)}
            >
              <Edit className="h-4 w-4" />
            </Button>
            <Button
              variant="outline"
              size="sm"
              onClick={() => handleDelete(vaccineId)}
            >
              <Trash2 className="h-4 w-4" />
            </Button>
          </div>
        )
      },
    },
  ]

  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold">Vaccine Management</h1>
        <Button onClick={() => setIsCreateModalOpen(true)}>
          <Plus className="mr-2 h-4 w-4" />
          Add Vaccine
        </Button>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>All Vaccines</CardTitle>
        </CardHeader>
        <CardContent>
          {loading ? (
            <div className="flex h-40 items-center justify-center">
              <p>Loading vaccines...</p>
            </div>
          ) : (
            <DataTable
              columns={columns}
              data={vaccines}
              searchColumn="title"
              searchPlaceholder="Search by vaccine name..."
            />
          )}
        </CardContent>
      </Card>

      {/* Create Vaccine Modal */}
      {isCreateModalOpen && (
        <VaccineModal
          onClose={async () => {
            setIsCreateModalOpen(false)
            await loadVaccines()
          }}
        />
      )}

      {/* Update Vaccine Modal */}
      {isUpdateModalOpen && (
        <VaccineModal
          onClose={async () => {
            setIsUpdateModalOpen(undefined)
            await loadVaccines()
          }}
          vaccine={isUpdateModalOpen}
        />
      )}
    </div>
  )
}
